.container{position:fixed;z-index:9999;bottom:24px;left:24px;max-width:500px;padding:18px;border-radius:9px;background:#fff;box-shadow:0px 0px 10px 5px rgba(41,57,77,.1);font-family:Roboto}.container_active{max-width:800px}.container .title{font-weight:500;color:#29394d;font-size:1.2rem}.container .text{font-size:14px;margin:1rem 0;color:#8d8d8d}.container .text p{text-align:left}.container .info{display:none;transition:all 1s;margin:1.5rem 0 1rem}.container .info_active{display:block}.container .info .cookiesDetails{overflow:scroll;max-height:500px}.container .info .cookiesDetails .section p{text-align:left;margin:0}.container .info .cookiesDetails .section_header{position:relative;padding:1rem 1.5rem;display:flex;align-items:center;justify-content:space-between}.container .info .cookiesDetails .section_header h2{font-size:1.125rem;font-weight:500;color:#29394d}.container .info .cookiesDetails .section_header .disable{position:absolute;height:100%;width:100px;top:0;right:0;background-color:rgba(255,255,255,.5529411765)}.container .info .cookiesDetails .section .description{font-size:.875rem;line-height:1.25rem;padding:0rem 1.5rem}.container .button{padding:.5rem 1.5rem;border:1px solid #18a0fb;color:#fff;text-align:center;text-decoration:none;display:inline-block;font-weight:500;font-family:Roboto;font-size:1rem;line-height:1.5;border-radius:4px;cursor:pointer;background:#18a0fb}.container .buttonInfo{padding:.5rem 1.5rem;color:#8d8d8d;text-align:center;text-decoration:none;display:inline-block;font-weight:500;font-family:Roboto;font-size:1rem;line-height:1.5;border-radius:4px;cursor:pointer;margin-right:10px;background:#f8f9fa;border:1px solid #b2bcc6;min-width:160px}.detail_box{margin:10px 0 20px;border-radius:8px;border:1px solid rgba(0,0,0,.04);background:#fff}.detail_box li{padding:10px;border-bottom:1px solid rgba(0,0,0,.04)}.detail_box li:last-child{border-bottom:none}.detail_title{font-weight:500;color:#29394d;display:flex;align-items:center;margin-bottom:5px}.detail_title img{margin-right:10px}.detail_text{font-size:14px;text-align:left;color:#8d8d8d}