.footer{margin-top:2rem}.copyright{height:2.75rem;display:flex;align-items:center;justify-content:center;font-size:.75rem;color:#6d7f92}.content{background-color:#fff;min-height:12.5rem;padding:2.5rem 5rem}.logos{height:100%;padding-top:.563rem}.uaLogo{height:2.375rem;width:8.813rem;margin-bottom:1.75rem;background-size:cover;background-position:center;background-repeat:no-repeat}.communityLogo,.communityLogoLarge{height:2.375rem;width:6.25rem;background-size:contain;background-position:center;background-repeat:no-repeat}.communityLogoLarge{height:6.375rem}.section{border-left:1px solid #f1f2f4;padding-top:.688rem}.section ul li{width:fit-content;cursor:pointer;height:1rem;margin-bottom:.625rem;font-size:.875rem;text-transform:lowercase;display:flex;align-items:center}.section ul li span::first-letter{text-transform:uppercase}.section ul li:last-child{margin-bottom:0rem}.section ul li:hover{color:#18a0fb}.section ul li:hover .block{background-color:#18a0fb}.block{width:10px;height:6px;background:#d8dde2;border-radius:0px 3px 3px 0px;margin-right:1.25rem}